// -- boilerplate -> to reset some the default style from the browser
html {
    font-size: 100%;
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
    background-color: var(--dark-bg);
    color: var(--dark-text2);
}
